.footer {
    min-height: 40vh;
    background-color: #a6dca6;
}

.footerContainer {
display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.footerBlock {
    width: 80%;
    border: 1px solid;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.footerBlockContent {
    background-color: darkgrey;
    width: 100px;
    height: 100px;
}



